import React, { Component } from 'react';
import { graphql } from 'gatsby';
import Slider from '../components/Slider/slider';
import Layout from '../components/layout';
import SEO from '../components/seo';
import ServicesList from '../components/ServicesList/serviceslist';
import Partners from '../components/PartnersList/partners';
import Header from '../components/Header/header';

export const query = graphql`
	{
		prismic {
			allServs {
				edges {
					node {
						name
						description
						image
						_linkType
					}
				}
			}
			allPartnerss {
				edges {
					node {
						title
						image
					}
				}
			}
			allSliders {
				edges {
					node {
						title
						description
						image
						imageSharp {
							childImageSharp {
								fluid {
									srcSetWebp
									srcWebp
									sizes
								}
							}
						}
					}
				}
			}
		}
	}
`;

export default class IndexPage extends Component {
	state = {
		services: this.props.data.prismic.allServs.edges,
		partners: this.props.data.prismic.allPartnerss.edges,
		sliders: this.props.data.prismic.allSliders.edges
	};

	render() {
		return (
			<Layout>
				<SEO title="Home" />
				<Slider data={this.state.sliders} />
				<Header />
				<ServicesList data={this.state.services} />
				<Partners data={this.state.partners} />
			</Layout>
		);
	}
}
