import React, { Component } from 'react';
import Swiper from 'react-id-swiper';
import './slider.css';
import Img from 'gatsby-image';

export default class Slider extends Component {
	state = {
		sliders: this.props.data
	};

	render() {
		const params = {
			navigation: {
				nextEl: '.swiper-button-next',
				prevEl: '.swiper-button-prev'
			}
		};
		return (
			<div className="slider">
				<Swiper {...params}>
					{this.state.sliders.map((slider, index) => {
						return (
							<img
								key={index}
								src={slider.node.imageSharp.childImageSharp.fluid.srcWebp}
								srcSet={slider.node.imageSharp.childImageSharp.fluid.srcSetWebp}
								alt={slider.node.title[0].text}
							/>
						);
					})}
				</Swiper>

				{/* {this.state.sliders.map((slider2) => {
					return (
						<Img
							fluid={slider2.node.imageSharp.childImageSharp.fluid}
							objectFit="cover"
							objectPosition="50% 50%"
						/>
					);
				})} */}
			</div>
		);
	}
}
